<template>
    <div class="row row-cols-1 row-cols-md-1 g-2">
        <div
            class="mt-4 p-5 text-white rounded"
            style="background-color: #5548a4; border-color: slateblue"
        >
            <div class="row">
                <div class="col">
                    <h1>Mis amigos</h1>
                </div>
                <div class="col text-end">
                    <button
                        type="button"
                        class="btn astra-btn-secundario"
                        @click="mostrarSolicitudes"
                        v-if="numeroSolicitudesPendientes > 0"
                    >
                        Ver solicitudes pendientes ({{
                            numeroSolicitudesPendientes
                        }})
                        <i class="fas fa-bell"> </i>
                    </button>
                    <button
                        type="button"
                        class="btn astra-btn-secundario disabled"
                        v-else
                    >
                        No hay solicitudes pendientes
                    </button>
                </div>
            </div>
            <p>
                Aqu√≠ puedes ver la lista de tus amigos y tus solicitudes
                pendientes
            </p>
        </div>
        <section v-if="verSolicitudes">
            <div
                class="mt-1 p-5 text-white rounded"
                style="background-color: #5548a4; border-color: slateblue"
            >
                <div class="row">
                    <div class="col">
                        <h1>Solicitudes Pendientes</h1>
                    </div>
                    <div class="col text-end">
                        <button
                            type="button"
                            class="btn astra-btn-secundario"
                            @click="this.verSolicitudes = !this.verSolicitudes"
                        >
                            Cerrar Panel
                            <i class="fas fa-times"></i>
                        </button>
                    </div>
                    <p>
                        <UsuarioCard
                            class="m-1"
                            v-for="usuarioPendiente in solicitudesPendientes"
                            :key="usuarioPendiente.id"
                            :usuario="usuarioPendiente.perfil_usuario"
                            :tipo="'solicitudes'"
                            @aceptarSolicitud="
                                aceptarSolicitud(usuarioPendiente.id)
                            "
                            @rechazarSolicitud="
                                rechazarSolicitud(usuarioPendiente.id)
                            "
                        />
                    </p>
                </div>
            </div>
        </section>
        <section v-if="listaAmigos">
            <UsuarioCard
                class="mb-1"
                v-for="amigo in listaAmigos"
                :key="amigo.id_lista"
                :usuario="amigo"
                :tipo="'listaAmigos'"
                @eliminarAmigo="modalEliminarAmigo(amigo)"
                @verPerfil="verPerfil(amigo.id_usuario)"
                @enviarMensaje="modalEnviarMensaje(amigo)"
            />
            <VueEternalLoading :load="listarAmigos"></VueEternalLoading>
            <div class="no-more text-light text-center fw-bold">
                <i class="fas fa-frown"></i> No hay m√°s amigos que mostrar.
                <i class="fas fa-frown"></i>
            </div>
        </section>
        <!-- MODAL ELIMINAR AMIGO -->
        <div
            class="modal fade"
            id="modalElimina"
            tabindex="-1"
            aria-labelledby="modalEliminaLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content" v-if="amigoSeleccionado">
                    <div class="modal-header">
                        <h5 class="modal-title" id="modalEliminaLabel">
                            Eliminar Amigo
                        </h5>
                        <button
                            type="button"
                            class="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        ¬øEstas seguro de eliminar a
                        {{ amigoSeleccionado.nombre }}?
                    </div>
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn astra-btn-primario"
                            data-bs-dismiss="modal"
                        >
                            No, me arrepent√≠
                        </button>
                        <button
                            type="button"
                            class="btn btn-danger"
                            data-bs-dismiss="modal"
                            @click="eliminarAmigo(amigoSeleccionado.id_lista)"
                        >
                            S√≠, ya no quiero que est√© en mi lista
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <!-- MODAL ENVIAR MENSAJE A AMIGO -->
        <div
            class="modal fade"
            id="modalMensaje"
            tabindex="-1"
            aria-labelledby="modalMensajeLabel"
            aria-hidden="true"
        >
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content" v-if="amigoSeleccionado">
                    <div class="modal-header">
                        <h5 class="modal-title" id="modalMensajeLabel">
                            Enviando mensaje a {{ amigoSeleccionado.nombre }}
                        </h5>
                        <button
                            type="button"
                            class="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div class="modal-body">
                        <div class="mb-3">
                            <label for="mensaje" class="form-label"
                                >Mensaje:</label
                            >
                            <textarea
                                class="form-control"
                                id="mensaje"
                                rows="3"
                                v-model="mensaje"
                            ></textarea>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn astra-btn-primario"
                            data-bs-dismiss="modal"
                        >
                            Cancelar
                        </button>
                        <button
                            type="button"
                            class="btn btn-danger"
                            data-bs-dismiss="modal"
                            @click="enviarMensaje(amigoSeleccionado.id_usuario)"
                            v-if="mensaje != ''"
                        >
                            Env√≠a mi mensaje
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { supabase } from "@/includes/supabase";
import UsuarioCard from "@/components/UsuarioCard.vue";
import { VueEternalLoading } from "@ts-pro/vue-eternal-loading";

export default {
    data() {
        return {
            numeroSolicitudesPendientes: 0,
            verSolicitudes: false,
            solicitudesPendientes: null,
            listaAmigos: [],
            indexAmigoInicio: 0,
            indexAmigoFin: 5,
            amigoSeleccionado: undefined,
            mensaje: "",
        };
    },
    components: {
        UsuarioCard,
        VueEternalLoading,
    },
    methods: {
        async mostrarSolicitudes() {
            this.verSolicitudes = !this.verSolicitudes;
            await this.listarSolicitudesPendientes();
        },

        async getSolicitudesPendientes() {
            try {
                const { count: pendientes, error } = await supabase
                    .from("lista_amigo")
                    .select("id_usuario2", { count: "exact" })
                    .match({
                        id_usuario2: supabase.auth.user().id,
                        confirmado: false,
                    });
                if (error) throw error;
                this.numeroSolicitudesPendientes = pendientes;
            } catch (error) {
                console.log(error);
            }
        },

        async listarSolicitudesPendientes() {
            try {
                const { data: pendientes, error } = await supabase
                    .from("lista_amigo")
                    .select("*, perfil_usuario!id_usuario1(*)")
                    .match({
                        id_usuario2: supabase.auth.user().id,
                        confirmado: false,
                    });
                this.solicitudesPendientes = pendientes;
                if (error) throw error;
            } catch (error) {
                console.log(error);
            }
        },

        async aceptarSolicitud(id) {
            try {
                const { data, error } = await supabase
                    .from("lista_amigo")
                    .update({
                        confirmado: true,
                        amigos_desde: new Date().toDateString(),
                    })
                    .match({ id: id });
                if (error) throw error;
                this.solicitudesPendientes = this.solicitudesPendientes.filter(
                    (sol) => sol.id != id
                );
                this.numeroSolicitudesPendientes--;
                this.haySolicitudes(true);
            } catch (error) {
                console.log(error);
            }
        },

        async rechazarSolicitud(id) {
            try {
                const { data, error } = await supabase
                    .from("lista_amigo")
                    .delete()
                    .match({ id: id });
                if (error) throw error;
                this.solicitudesPendientes = this.solicitudesPendientes.filter(
                    (sol) => sol.id != id
                );
                this.numeroSolicitudesPendientes--;
                this.haySolicitudes();
            } catch (error) {
                console.log(error);
            }
        },

        async listarAmigos({ loaded }) {
            try {
                const { data: amigos, error } = await supabase
                    .rpc("lista_de_amigos", {
                        id_usuario: supabase.auth.user().id,
                    })
                    .range(this.indexAmigoInicio, this.indexAmigoFin);
                if (error) throw error;
                if (amigos.length) {
                    this.listaAmigos.push(...amigos);
                    this.indexAmigoInicio += 5;
                    this.indexAmigoFin += 5;
                    loaded(amigos.length, 5);
                }
            } catch (error) {
                console.log(error);
            }
        },

        modalEliminarAmigo(amigo) {
            this.amigoSeleccionado = amigo;
        },

        async eliminarAmigo(idAmigoEnLista) {
            try {
                const { error } = await supabase
                    .from("lista_amigo")
                    .delete()
                    .match({ id: idAmigoEnLista });
                if (error) throw error;
                this.listaAmigos = this.listaAmigos.filter(
                    (amigo) => amigo.id_lista != idAmigoEnLista
                );
            } catch (error) {
                console.log(error);
            }
        },

        verPerfil(idUsuario) {
            this.$router.push({
                name: "Perfil",
                params: { idUsuario: idUsuario },
            });
        },

        modalEnviarMensaje(amigo) {
            this.amigoSeleccionado = amigo;
        },

        async enviarMensaje(idUsuarioAmigo) {
            if (this.mensaje != "") {
                try {
                    const { error } = await supabase.from("mensaje").insert({
                        usuario_envia: supabase.auth.user().id,
                        usuario_recibe: idUsuarioAmigo,
                        contenido: this.mensaje,
                    });
                    if (error) throw error;
                    // TODO: Agregar l√≥gica para que muestre una alerta de mensaje enviado
                } catch (error) {
                    console.log(error);
                }
            }
            this.mensaje = "";
        },

        haySolicitudes(acepta) {
            if (this.numeroSolicitudesPendientes == 0) {
                this.verSolicitudes = !this.verSolicitudes;
                if (acepta) {
                    this.$router.go();
                }
            }
        },
    },
    async created() {
        await this.getSolicitudesPendientes();
    },
};
</script>
<style scoped>
.astra-btn-primario {
    background-color: slateblue;
    border-color: slateblue;
    color: whitesmoke;
}

.astra-btn-primario:hover {
    background-color: whitesmoke;
    border-color: slateblue;
    color: black;
}

.astra-btn-secundario {
    background-color: whitesmoke;
    border-color: slateblue;
    color: black;
}
.astra-btn-secundario:hover {
    background-color: slateblue;
    border-color: slateblue;
    color: whitesmoke;
}
</style>